<template>
  <div class="moduleBoxView">
    <div class="moduleFrom">
      <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="80" label-position="right">
        <el-row :gutter="0">
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.afterOrderNumber')">
              <el-input v-model="fromData.afterOrderNumber" :placeholder="$t('Careof.afterOrderNumberPlaceholder')" style="width: 100%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.problemTitle')">
              <el-select v-model="fromData.problemId" filterable :placeholder="$t('Careof.problemTitlePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.problemList" :key="item.id" :label="item.title" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.spuName')">
              <el-input v-model="fromData.spuName" :placeholder="$t('Careof.spuNamePlaceholder')"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.function')">
              <el-select filterable v-model="fromData.function" :placeholder="$t('Careof.functionPlaceholder')" >
                <el-option v-for="item in selectData.functionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.series')">
              <el-select filterable v-model="fromData.series" :placeholder="$t('Careof.seriesPlaceholder')">
                <el-option v-for="item in selectData.seriesList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.familyName')">
              <el-input v-model="fromData.familyName" :placeholder="$t('Careof.familyNamePlaceholder')"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.buyDitchName')">
              <el-select filterable v-model="fromData.buyDitch" :placeholder="$t('Careof.buyDitchNamePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.region')">
              <el-select v-model="fromData.region" filterable :placeholder="$t('Careof.regionPlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.agoUser')">
              <el-select filterable v-model="fromData.agoUser" :placeholder="$t('Careof.agoUserPlaceholder')">
                <el-option v-for="item in selectData.userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.nowUser')">
              <el-select filterable v-model="fromData.nowUser" :placeholder="$t('Careof.nowUserPlaceholder')">
                <el-option v-for="item in selectData.userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('Careof.careofStatus')">
              <el-select filterable v-model="fromData.careofStatus" :placeholder="$t('Careof.careofStatusPlaceholder')">
                <el-option v-for="item in selectData.careofStatusList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" />
            <el-form-item>
              <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
              <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>

    <div class="moduletTable" ref="tableDiv">
      <el-table :data="tableData" :height="tabHeight" stripe border fit highlight-current-row :empty-text="$t('common.table.noData')" style="width: 100%; margin-bottom: 5px;">
        <el-table-column fixed type="index" align="center" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="afterOrderNumber" :label="$t('Careof.afterOrderNumber')" fixed="left" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="disposeUserNmae" :label="$t('Careof.disposeUser')" width="280" fixed="left"  show-overflow-tooltip>
          <template v-slot="{ row }">
            <el-tag class="ml-2" >{{ row.agoUserNmae }}</el-tag>
            <el-icon size="16" class="regicon"><Right /></el-icon>
            <el-tag class="ml-2" type="success">{{ row.nowUserNmae }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="problemTitle" :label="$t('Careof.problemTitle')" width="130" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spuName" :label="$t('Careof.spuName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="functionName" :label="$t('Careof.function')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="seriesName" :label="$t('Careof.series')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="familyName" :label="$t('Careof.familyName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="summary" :label="$t('Careof.summary')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="buyDitchName" :label="$t('Careof.buyDitchName')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="regionName" :label="$t('Careof.region')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createTime" :label="$t('Careof.createTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="revampTime" :label="$t('Careof.revampTime')" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="afterStatus" :label="$t('Careof.careofStatus')" width="170" fixed="right" align="center" show-overflow-tooltip>
          <template v-slot="{ row }">
            <el-tag v-show="row.careofStatus == 1">{{ $t('pullDown.careofStatus.processing') }}</el-tag>
            <el-tag class="ml-2" v-show="row.careofStatus == 2" type="success">{{ $t('pullDown.careofStatus.completed') }}</el-tag>
            <el-tag class="ml-2" v-show="row.careofStatus == 3" type="danger">{{ $t('pullDown.careofStatus.refused') }}</el-tag>
            <el-tag class="ml-2" v-show="row.careofStatus == 4" type="info">{{ $t('pullDown.careofStatus.cancel') }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="250">
          <template #default="scope">
            <el-button @click="clickButton(scope.row, 'detail')" type="primary" size="small" plain> {{ $t('common.button.detail') }} </el-button>
            <el-button v-show="scope.row.agoUser == userId && scope.row.careofStatus == 1" @click="clickButton(scope.row, 'cancel')" type="info" plain size="small">
              {{ $t('common.button.cancel') }}
            </el-button>
            <el-button v-show="scope.row.nowUser == userId && scope.row.careofStatus == 1" type="success" size="small" @click="clickButton(scope.row, 'accept')" plain>{{ $t('common.button.accept') }}</el-button>
            <el-button v-show="scope.row.nowUser == userId && scope.row.careofStatus == 1" type="danger" size="small" @click="clickButton(scope.row, 'refuse')" plain >{{ $t('common.button.refuse') }}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="modulePagination">
      <el-pagination
          background
          v-model:current-page="pageNum"
          v-model:page-size="pageSize"
          :page-sizes="[10, 30, 50, 75, 100]"
          :small="false"
          layout="prev, pager, next, sizes, jumper, total"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>

    <afterCareof-operate ref="operate"/>
  </div>
</template>

<script>
import {resourceIsUser} from "@/util/resource";
import {ElLoading, ElNotification} from "element-plus";
import {getFunctionPullDown, getSeriesPullDown, getUserAdminPullDown} from "@/http/PullDownApi";
import {getDictPullDownClientele, getRegionPullDown, listProblemClientele} from "@/http/clienteleApi";
import {listAfterCareof, updateAfterCareof} from "@/http/spuOred/careofApi";
import afterCareofOperate from "@/views/spuOred/afterCareof/afterCareof-operate";

export default {
  name: "styleVeiw",
  components: {afterCareofOperate},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      tabHeight: 0, // 表格高度
      // 当前登录用户的id
      userId: 0,
      // 查询框数据集合对象
      selectData: {
        careofStatusList: [
          {id: 1, name: this.$t('pullDown.careofStatus.processing')},
          {id: 2, name: this.$t('pullDown.careofStatus.completed')},
          {id: 3, name: this.$t('pullDown.careofStatus.refused')},
          {id: 4, name: this.$t('pullDown.careofStatus.cancel')},
        ],
      },
      // 查询条件
      fromData: {},

      tableData: [],
      total: 0,           // 总条数
      pages: 0,           // 总页数
      pageNum: 1,         //当前页码
      pageSize: 30,        // 每页条数
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    // 获取当前登录用户的id
    this.userId = JSON.parse(window.localStorage.getItem("account")).id;
    this.refreshTableHeight();
    this.getSelectData();
    this.listData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 刷新表格高度
    refreshTableHeight() {
      // 创建一个 ResizeObserver 实例
      const resizeObserver = new ResizeObserver(entries => {
        // entries 是 ResizeObserverEntry 对象的数组，包含观察到的元素信息
        for (const entry of entries) {
          if (entry.target === this.$refs.tableDiv) {
            // 如果观察到的元素是你要监听的元素
            // console.log('窗口大小改变，新的表格高度：', entry.contentRect.height);
            this.tabHeight = this.$refs.tableDiv.offsetHeight - 20;
          }
        }
      });
      // 将 ResizeObserver 注册到要观察的元素
      resizeObserver.observe(this.$refs.tableDiv);
    },
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    // 获取查询条件数据
    getSelectData() {
      getSeriesPullDown().then(res => {
        if (res.data.status == 200) this.selectData.seriesList = res.data.data;
      }).catch((erro) => { });
      getFunctionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.functionList = res.data.data;
      }).catch((erro) => { });
      getUserAdminPullDown().then(res => {
        if (res.data.status == 200) this.selectData.userList = res.data.data;
      }).catch((erro) => { });

      // 售后类型
      listProblemClientele().then(res => {
        if (res.data.status == 200) this.selectData.problemList = res.data.data;
      }).catch((erro) => { });
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 渠道
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchList = res.data.data;
      }).catch((erro) => { });
    },

    // 列表数据请求
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      this.fromData.pageSize = this.pageSize;
      this.fromData.pageNum = this.pageNum;
      listAfterCareof(this.fromData).then(res => {
        this.tableData = res.data.data.list;
        // 给分页组件的数据赋值
        this.total = res.data.data.total;
        this.pages = res.data.data.pages;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    // 设置每页条数方法 val=条数
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageNum = 1;                       // 修改当前页为第1页
      this.pageSize = val;               // 修改每页条数
      this.listData();
    },
    // 点击 前一页、后一页、页码 事件
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pageNum = val;                 // 修改当前页码
      this.listData();
    },
    // 重置按钮
    reset() {
      this.fromData = {};
      this.listData();
    },

    // 详情 / 取消 ... 按钮执行方法
    clickButton(row, type) {
      if (type == 'cancel') {
        // 取消转交
        this.$confirm(this.$t('Careof.transferTxt'), this.$t('common.dialog.title'),
            {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}
        ).then(() => {
          row.careofStatus = 4;
          updateAfterCareof(row).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              // 刷新列表
              this.listData();
            }
          }).catch((erro) => {});
        })
      } else if (type == 'accept') {
        // 接受转交
        this.$confirm(this.$t('Careof.receiveTxt'), this.$t('common.dialog.title'),
            {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'success'}
        ).then(() => {
          row.careofStatus = 2;
          updateAfterCareof(row).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              // 刷新列表
              this.listData();
            }
          }).catch((erro) => {});
        })
      } else if (type == 'refuse') {
        // 拒绝转交
        this.$confirm(this.$t('Careof.refuseTxt'), this.$t('common.dialog.title'),
            {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}
        ).then(() => {
          row.careofStatus = 3;
          updateAfterCareof(row).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              // 刷新列表
              this.listData();
            }
          }).catch((erro) => {});
        })
      } else if (type == 'detail') {
        this.$refs.operate.dialogFormVisible = true;
        this.$refs.operate.type = type;
        this.$refs.operate.rowData = row;
        this.$refs.operate.gotoExecute();
      }
    },


  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.regicon {
  /*垂直居中*/
  vertical-align: middle;
  margin: 0 5px 0 5px;
}
</style>
