import request from '../request'


// 新增 工单转交
export function saveAfterCareof(data) {
    return request({
        url: '/api/afterCareof/saveAfterCareof',
        method: 'post',
        data
    })
}

// 获取列表
export function listAfterCareof(data) {
    return request({
        url: '/api/afterCareof/listAfterCareof',
        method: 'post',
        data
    })
}

// 修改
export function updateAfterCareof(data) {
    return request({
        url: '/api/afterCareof/updateAfterCareof',
        method: 'post',
        data
    })
}

// 根据ID查询详情
export function getAfterCareofById(params) {
    return request({
        url: '/api/afterCareof/getAfterCareofById',
        method: 'get',
        params: params
    });
}



















