<template>
  <div id="style-operate">
    <!-- 对话框 -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="900" draggable>
      <el-row :gutter="15">
        <el-col :span="10"><div class="grid-content ep-bg-purple" />
          <el-card class="box-card" shadow="never">
            <template #header>
              <div class="card-header">
                <span>{{ $t('Careof.afterOrderNumber') }}</span>
              </div>
            </template>
            <div class="text item">
              <el-form-item :label="$t('Careof.afterOrderNumber')">
                <el-input v-model="form.afterOrderNumber" :readonly="true"></el-input>
              </el-form-item>
              <el-form-item :label="$t('Careof.problemTitle')">
                <el-input v-model="form.problemTitle" :readonly="true"></el-input>
              </el-form-item>
              <el-form-item :label="$t('Careof.spuName')">
                <el-input v-model="form.spuName" :readonly="true"></el-input>
              </el-form-item>
              <el-form-item :label="$t('Careof.function')">
                <el-input v-model="form.functionName" :readonly="true"></el-input>
              </el-form-item>
              <el-form-item :label="$t('Careof.series')">
              <el-input v-model="form.seriesName" :readonly="true"></el-input>
            </el-form-item>
              <el-form-item :label="$t('Careof.familyName')">
                <el-input v-model="form.familyName" :readonly="true"></el-input>
              </el-form-item>
              <el-form-item :label="$t('Careof.summary')">
                <el-input v-model="form.summary" :readonly="true"></el-input>
              </el-form-item>
            </div>
          </el-card>
        </el-col>

        <el-col :span="14"><div class="grid-content ep-bg-purple" />
          <el-card class="box-card" shadow="never">
            <template #header>
              <div class="card-header">
                <span>{{ $t('Careof.afterTitle') }}</span>
                <div class="button" text>
                  <el-tag class="ml-2" >{{ form.agoUserNmae }}</el-tag>
                  <el-icon size="16" class="regicon"><Right /></el-icon>
                  <el-tag class="ml-2" type="success">{{ form.nowUserNmae }}</el-tag>
                </div>
              </div>
            </template>
            <div>
              <el-timeline>
                <el-timeline-item color="#409eff" icon="CircleCheckFilled" size="large" :timestamp="form.createTime">
                  {{ $t('Careof.transferTitle') }} -
                  <el-tag class="ml-2" >{{ form.agoUserNmae }}</el-tag>
                </el-timeline-item>
                <el-timeline-item color="#67c23a" icon="List" size="large" :timestamp="form.createTime">
                  {{ $t('Careof.transferingTitle') }} -
                  <el-tag class="ml-2" >{{ form.agoUserNmae }}</el-tag>
                </el-timeline-item>

                <el-timeline-item v-if="form.careofStatus == 2" color="#68c23b" icon="List" size="Pointer" :timestamp="form.revampTime">
                  {{ $t('Careof.transferSuccessTitle') }} -
                  <el-tag class="ml-2" type="success">{{ form.nowUserNmae }}</el-tag>
                </el-timeline-item>
                <el-timeline-item v-if="form.careofStatus == 3" color="#f89898" icon="List" size="MuteNotification" :timestamp="form.revampTime">
                  {{ $t('Careof.transferRefusedTitle') }} -
                  <el-tag class="ml-2" type="success">{{ form.nowUserNmae }}</el-tag>
                </el-timeline-item>
                <el-timeline-item v-if="form.careofStatus == 4" color="#b1b3b8" icon="List" size="<el-icon><Promotion /></el-icon>" :timestamp="form.revampTime">
                  {{ $t('Careof.transferCancelTitle') }} -
                  <el-tag class="ml-2" >{{ form.agoUserNmae }}</el-tag>
                </el-timeline-item>

                <el-timeline-item v-if="form.careofStatus != 1" color="#eebe77" icon="Sort" size="large" :timestamp="form.revampTime">
                  {{ $t('Careof.transferResultTitle') }} -
                  <el-tag class="ml-2" v-show="form.careofStatus == 2" type="success">{{ $t('pullDown.careofStatus.completed') }}</el-tag>
                  <el-tag class="ml-2" v-show="form.careofStatus == 3" type="danger">{{ $t('pullDown.careofStatus.refused') }}</el-tag>
                  <el-tag class="ml-2" v-show="form.careofStatus == 4" type="info">{{ $t('pullDown.careofStatus.cancel') }}</el-tag>
                </el-timeline-item>
                <el-timeline-item v-if="form.careofStatus != 1" color="#6b6d71" icon="LocationFilled" size="large" :timestamp="form.revampTime">
                  {{ $t('Careof.transferEndTitle') }} -
                  <el-tag v-show="form.careofStatus == 4" class="ml-2" >{{ form.agoUserNmae }}</el-tag>
                  <el-tag v-show="form.careofStatus != 4" class="ml-2" type="success">{{ form.nowUserNmae }}</el-tag>
                </el-timeline-item>
              </el-timeline>
            </div>
          </el-card>
        </el-col>
      </el-row>

      <template #footer>
      <span class="dialog-footer">
        <!-- Form 按钮 -->
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {getAfterCareofById} from "@/http/spuOred/careofApi";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据 - 行数据副本
      rowData: {},
      form: {},
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '120px',
      pullDownData: {}, // 查询框数据集合对象
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
        if (newVal == '') return
        if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
        if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
        if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    selectData() {

    },
    // 父组件进入子组件时执行
    gotoExecute() {
      getAfterCareofById({id:this.rowData.id}).then(res => {
        this.form = res.data.data
      })
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible= false;
      this.isReadonly= false;
      this.form= {};
      this.rowData= {};
    },
    // 确认按钮
    submitButton() {

    }

  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/* 对话框的内边距覆盖组件 */
/deep/ .el-dialog__body {
  padding-top: 15px;
  padding-bottom: 5px;

}

/* 组件卡片样式 */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.box-card {
  width: 100%;
  height: 445px;
}

.regicon {
  /*垂直居中*/
  vertical-align: middle;
  margin: 0 5px 0 5px;
}
</style>
